@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}
.ColorWheel {
  position: absolute;
  top: calc(-200vmax + 3em);
  left: calc(-200vmax + 3em);
  opacity: 0.5;
  width: 400vmax;
  height: 400vmax;
  animation: spin 300s linear infinite;
}

.ColorWheel.reverse {
  animation-direction: reverse;
  top: calc(100vh - 200vmax - 3em);
  left: calc(100vw - 200vmax - 3em);
  transform: translateY(200px);
}

.Radial {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  clip-path: polygon(49% 1%, 50% 0, 51% 1%, 50% 50%);
}
