html {
  overflow: scroll;
  overflow-x: hidden;
}
::-webkit-scrollbar {
  width: 0px;
  background: transparent;
}
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@-webkit-keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
  }
}
@keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
  }
}
.ColorWheel {
  position: absolute;
  top: calc(-200vmax + 3em);
  left: calc(-200vmax + 3em);
  opacity: 0.5;
  width: 400vmax;
  height: 400vmax;
  -webkit-animation: spin 300s linear infinite;
          animation: spin 300s linear infinite;
}

.ColorWheel.reverse {
  animation-direction: reverse;
  top: calc(100vh - 200vmax - 3em);
  left: calc(100vw - 200vmax - 3em);
  -webkit-transform: translateY(200px);
          transform: translateY(200px);
}

.Radial {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  -webkit-clip-path: polygon(49% 1%, 50% 0, 51% 1%, 50% 50%);
          clip-path: polygon(49% 1%, 50% 0, 51% 1%, 50% 50%);
}

:root {
  --text-color: hsl(240, 25%, 85%);
}
.App {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: black;
}

a {
  color: hsl(240, 25%, 85%);
  color: var(--text-color);
}
.About {
  z-index: 0;
  color: hsl(240, 25%, 85%);
  color: var(--text-color);
  background-image: -webkit-gradient(
    linear,
    left top, left bottom,
    from(hsla(240, 50%, 10%, 0.95)),
    color-stop(75%),
    color-stop(hsla(240, 50%, 50%, 0.7)),
    color-stop(98%),
    to(hsla(240, 100%, 75%, 0.9))
  );
  background-image: linear-gradient(
    hsla(240, 50%, 10%, 0.95),
    75%,
    hsla(240, 50%, 50%, 0.7),
    98%,
    hsla(240, 100%, 75%, 0.9)
  );
  width: calc(100vw - 6em);
  height: calc(100vh - 6em);
  border: 1px solid white;
  border-radius: 1em;
}

article > header {
  display: flex;
  align-content: stretch;
  font-size: 2em;
  font-weight: bold;
  border-bottom: 1px solid white;
}

.title,
.email {
  flex: 1 1;
  margin: 0.5rem 1rem 0.5rem 1rem;
}
.email {
  text-align: right;
}

section {
  font-size: 1.25em;
  margin: 0.5em 1em 0.5em 1em;
  header {
    font-weight: bold;
  }
}
